.feedbackList {
    column-count: 2;
    // column-fill: auto;
    column-gap: 16px;
}
.feedbackItem {
    display: block;
    margin-bottom: 16px;
    text-align: center;
    break-inside: avoid-column;
    a {
        display: block;
        background-color: $color-white;
        padding: 28px;
    }

}
.feedbackItemFbIcon {
    font-size: 88px;
    margin-bottom: 24px;
    color: $color-dark-green;
}
.feedbackItemTitle {
    font-size: 16px;
    margin-bottom: 24px;
}
.feedbackItemText {
    font-size: 12px;
    line-height: 32px;
}
@media (max-width: 991.98px) { 
    .feedbackList {
        // column-fill: auto;
        column-gap: 12px;
    }
    .feedbackItem {
        display: block;
        margin-bottom: 12px;
        text-align: center;
        break-inside: avoid-column;
    }
    .feedbackItemFbIcon {
        font-size: 80px;
        margin-bottom: 20px;
        color: $color-dark-green;
    }
    .feedbackItemTitle {
        font-size: 14px;
        line-height: 28px;
        margin-bottom: 20px;
    }
    .feedbackItemText {
        font-size: 12px;
        line-height: 28px;
    }
};

// Small devices (landscape phones, less than 768px)
@media (max-width: 767.98px) {
    .feedbackList {
        column-count: 1;
        // column-fill: auto;
        column-gap: 12px;
    }
    .feedbackItem {
        a {
            padding: 24px;
        }
    }
    .feedbackItemFbIcon {
        font-size: 80px;
        margin-bottom: 20px;
        color: $color-dark-green;
    }
    .feedbackItemTitle {
        font-size: 14px;
        line-height: 28px;
        margin-bottom: 20px;
    }
    .feedbackItemText {
        font-size: 12px;
        line-height: 28px;
    }
};